import React, {useState} from 'react';
import './App.css';

let conArr = {
  'nav01':"컨포넌트01",
  'nav02':"컨포넌트02"
}

function App() {

  let [cpnt, setCpnt] = useState(conArr['nav01']);

  function Nav(e){
    if(e.target.tagName == 'A'){
      let menuId = e.target.textContent;
      setCpnt(conArr[menuId]);
    }
    

    return(
      {cpnt}
    );
  }
  
  return (
    <>
    <header onClick={Nav}>
      <a>nav01</a>
      <a>nav02</a>
    </header>
    <main>
      <div>
      {cpnt}
      </div>
    </main>
    </>
  );
}

export default App;
